
.movie_detail .movie_background{width: 100%;position: absolute;top: 0;left: 0;z-index: -1;}
.movie_detail .movie_background::after{content:'';display: block;width: 100%;height: 100%;background: rgba(0, 0, 0, 0.5);}
.movie_detail .movie_background img{width: 100%;}
.movie_detail .movie_container{display: flex;justify-content: space-between;padding-top: 190px;}

.movie_container .movie_text{width: calc(100% - 490px);padding-left: 102px;box-sizing: border-box;}
.movie_container .movie_text .title{font-size: 52px;padding-bottom: 30px;}
.movie_container .movie_text .year{font-weight: 400;color:#BDC2C8;font-size: 24px;padding-left: 10px;}
.movie_container .movie_text .genres{padding-bottom: 40px;}
.movie_container .movie_text .genres li{display: inline-block;padding:10px 25px;font-size: 10px;background: rgba(88, 84, 85, 0.5);border-radius:5px;margin-right: 5px;}
.movie_container .movie_text .desc{font-size: 13px;line-height: 2;width: 585px;padding-bottom: 30px;}
.movie_container .movie_text .rating{display: flex;align-items: center;}
.movie_container .movie_text .rating dt{font-size: 14px;color:#AFB1BE}
.movie_container .movie_text .rating dd{font-size: 32px;font-weight: 700;}
.movie_container .movie_poster{width: 400px;height: 580px;border-radius: 5px;overflow: hidden;margin-right: 90px;box-shadow: 0px 47px 28px -37px rgba(0,0,0,0.71);}
.movie_container .movie_poster img{width: 100%;}