.movies_wrap {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.movies {
	width: 80%;
}

.movies + .movies {
	padding-top: 100px;
}
.movies .title {
	font-family: "Spartan", sans-serif;
	padding-bottom: 65px;
	position: relative;
	font-weight: 700;
}
.movies .title::after {
	content: "";
	display: inline-block;
	width: calc(100% - 250px);
	background: #282830;
	height: 2px;
	position: absolute;
	right: 0;
	top: 15px;
}

.movies input {
	margin-bottom: 20px;
}

.movie_list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.movie_item {
	padding-bottom: 80px;
}
.movie_item .movie_poster {
	width: 290px;
	height: 380px;
	overflow: hidden;
	border-radius: 8px;
	position: relative;
	box-shadow: 0px 47px 28px -37px rgba(0, 0, 0, 0.71);
	transition: all 0.2s;
}
.movie_item .movie_poster img {
	width: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
.movie_item:hover .movie_poster {
	box-shadow: 0px 55px 28px -37px rgba(0, 0, 0, 8);
	transform: translateY(-10px);
}

.movie_item .movie_genres {
	padding: 20px 0;
	position: relative;
	z-index: 0;
}
.movie_item .movie_genres li {
	display: inline-block;
	font-size: 8px;
	background: #585455;
	padding: 7px 20px 5px;
	border-radius: 5px;
	margin-right: 5px;
}
.movie_item .movie_title {
	font-family: "Spartan", sans-serif;
	font-weight: 400;
	font-size: 15px;
}
