@import url(https://fonts.googleapis.com/css?family=Spartan:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
* {
	padding: 0;
	margin: 0;
}
body {
	background: #1a1a1a;
	padding: 0;
	margin: 0;
	color: #fff;
	font-family: "Spartan", sans-serif;
}
a {
	text-decoration: none;
	color: #fff;
}
li {
	list-style: none;
}

.loading {
	display: flex;
	width: 100%;
	height: 200px;
	align-items: center;
	justify-content: center;
}

.movies_wrap {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.movies {
	width: 80%;
}

.movies + .movies {
	padding-top: 100px;
}
.movies .title {
	font-family: "Spartan", sans-serif;
	padding-bottom: 65px;
	position: relative;
	font-weight: 700;
}
.movies .title::after {
	content: "";
	display: inline-block;
	width: calc(100% - 250px);
	background: #282830;
	height: 2px;
	position: absolute;
	right: 0;
	top: 15px;
}

.movies input {
	margin-bottom: 20px;
}

.movie_list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.movie_item {
	padding-bottom: 80px;
}
.movie_item .movie_poster {
	width: 290px;
	height: 380px;
	overflow: hidden;
	border-radius: 8px;
	position: relative;
	box-shadow: 0px 47px 28px -37px rgba(0, 0, 0, 0.71);
	transition: all 0.2s;
}
.movie_item .movie_poster img {
	width: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
.movie_item:hover .movie_poster {
	box-shadow: 0px 55px 28px -37px rgba(0, 0, 0, 8);
	transform: translateY(-10px);
}

.movie_item .movie_genres {
	padding: 20px 0;
	position: relative;
	z-index: 0;
}
.movie_item .movie_genres li {
	display: inline-block;
	font-size: 8px;
	background: #585455;
	padding: 7px 20px 5px;
	border-radius: 5px;
	margin-right: 5px;
}
.movie_item .movie_title {
	font-family: "Spartan", sans-serif;
	font-weight: 400;
	font-size: 15px;
}

.header {
	position: relative;
	height: 500px;
	background: url(/movie_app_2020/static/media/header-bg.1fd572d4.jpg) center center;
	display: flex;
	align-items: center;
	justify-content: center;
}

.header::after {
	content: "";
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	left: 0;
	top: 0;
}

.gradient {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 200px;
	background: linear-gradient(to bottom, rgba(26, 26, 26, 0) 0%, rgba(26, 26, 26, 0) 0%, rgba(26, 26, 26, 1) 61%, rgba(26, 26, 26, 1) 100%);
	z-index: 1;
}

.header .title {
	position: relative;
	z-index: 1;
	width: 80%;
	text-transform: uppercase;
	font-family: "Roboto", sans-serif;
}

.header .title span {
	display: block;
	font-size: 18px;
	letter-spacing: 3px;
	font-weight: 700;
}

.header .title h1 {
	font-weight: 900;
	font-size: 100px;
}

.header .search {
	position: absolute;
	right: 20px;
	top: 20px;
	z-index: 1;
	border: 1px solid #999;
	border-radius: 50px;
	display: flex;
	align-items: center;
	padding: 10px 15px 10px 20px;
}

.header .search input {
	background: transparent;
	border: none;
	color: #fff;
	text-transform: uppercase;
}

.header .search button {
	width: 20px;
	background: transparent;
	border: none;
	margin-left: 20px;
	cursor: pointer;
}

.header .search input:focus {
	color: #fff;
	outline: none;
}

@media screen and (max-width: 1920px) {
	.header {
		background-size: cover;
	}
}


.movie_detail .movie_background{width: 100%;position: absolute;top: 0;left: 0;z-index: -1;}
.movie_detail .movie_background::after{content:'';display: block;width: 100%;height: 100%;background: rgba(0, 0, 0, 0.5);}
.movie_detail .movie_background img{width: 100%;}
.movie_detail .movie_container{display: flex;justify-content: space-between;padding-top: 190px;}

.movie_container .movie_text{width: calc(100% - 490px);padding-left: 102px;box-sizing: border-box;}
.movie_container .movie_text .title{font-size: 52px;padding-bottom: 30px;}
.movie_container .movie_text .year{font-weight: 400;color:#BDC2C8;font-size: 24px;padding-left: 10px;}
.movie_container .movie_text .genres{padding-bottom: 40px;}
.movie_container .movie_text .genres li{display: inline-block;padding:10px 25px;font-size: 10px;background: rgba(88, 84, 85, 0.5);border-radius:5px;margin-right: 5px;}
.movie_container .movie_text .desc{font-size: 13px;line-height: 2;width: 585px;padding-bottom: 30px;}
.movie_container .movie_text .rating{display: flex;align-items: center;}
.movie_container .movie_text .rating dt{font-size: 14px;color:#AFB1BE}
.movie_container .movie_text .rating dd{font-size: 32px;font-weight: 700;}
.movie_container .movie_poster{width: 400px;height: 580px;border-radius: 5px;overflow: hidden;margin-right: 90px;box-shadow: 0px 47px 28px -37px rgba(0,0,0,0.71);}
.movie_container .movie_poster img{width: 100%;}
